import { Component, OnInit } from '@angular/core';

declare  var $:any;
declare  var hero:any;
@Component({
  selector: 'hero-page',
  templateUrl: './hero-page.component.html',
  styleUrls: ['./hero-page.component.css']
})
export class HeroPageComponent implements OnInit {

  public bzList = [];
  public mjList = []; //敏捷型英雄列表
  public llList = []; //力量型英雄列表
  public zlList = []; //智力型英雄列表

  constructor() {

  }

  ngOnInit() {
    this.initHeroList();
  }

  /*获取所有英雄列表数据*/
  initHeroList(){
    var that = this;
    $.getScript("http://cos.99.com/data/info/script/hero_new.js",function(){
      var arr = []
      for (let i in hero) {
        arr.push(hero[i]);
      }
      that.bzList = arr;

      /*英雄分类型归组*/
      for(var i=0;i<that.bzList.length;i++){
        if(that.bzList[i].type=="m"){
          that.mjList.push(that.bzList[i]);
        }else if(that.bzList[i].type=="l"){
          that.llList.push(that.bzList[i]);
        }else if(that.bzList[i].type=="z"){
          that.zlList.push(that.bzList[i]);
        }
      }

    });
  }

}
